<template>
    <div class="boss">
        <!-- <van-tabs v-model="active" @click="selectTab">
            <van-tab v-for="(item, index) of tabs"
                :title="item"
                :key="index"></van-tab>
        </van-tabs> -->
        <div class="section">
            <div class="tabs">
                <div class="tab-wrapper" v-for="(tab, index) in tabs" :key="index" >
                    <div class="tab"
                        @click="selectTab1(index)"
                        :class="current1===index?'active':''">{{tab}}</div>
                </div>
            </div>
            <!-- boss数据 -->
            <div class="cards">
                <div class="item">
                    <div class="count">
                        <h2 class="text">商城订单数</h2>
                        <div v-if="bossData" class="num">{{bossData.ordercount}}<span class="t"></span></div>
                    </div>
                    <div class="count">
                        <h2 class="text">商城销售金额</h2>
                        <div v-if="bossData" class="num">{{bossData.orderprice}}<span class="t"></span></div>
                    </div>
                    <div class="count">
                        <h2 class="text">客户总数</h2>
                        <div v-if="bossData" class="num">{{bossData.recommendcount}}<span class="t"></span></div>
                    </div>
                </div>
            </div>
            <div class="cards">
                <div class="item">
                    <div class="count">
                        <h2 class="text">微信咨询数</h2>
                        <div v-if="bossData" class="num">{{bossData.sharecount}}<span class="t"></span></div>
                    </div>
                    <div class="count">
                        <h2 class="text">被转发总数</h2>
                        <div v-if="bossData" class="num">{{bossData.telcount}}<span class="t"></span></div>
                    </div>
                    <div class="count">
                        <h2 class="text">分销人总数</h2>
                        <div v-if="bossData" class="num">{{bossData.usercount}}<span class="t"></span></div>
                    </div>
                </div>
            </div>
            <div class="cards">
                <div class="item">
                    <div class="count">
                        <h2 class="text">电话咨询数</h2>
                        <div v-if="bossData" class="num">{{bossData.weixincount}}<span class="t"></span></div>
                    </div>
                    <div class="count bg">

                    </div>
                    <div class="count bg">

                    </div>
                </div>
            </div>
        </div>
        <!-- 客户感兴趣的产品排行 -->
        <div class="section">
            <h2 class="title">客户感兴趣的产品排行</h2>
            <ve-pie :data="chartData1" :settings="chartSetting1" style="height: 320px;"></ve-pie>
        </div>
        <!-- 商城订单量&交易金额 -->
        <div class="section">
            <h2 class="title">商城订单量&交易金额</h2>
            <div class="tabs">
                <div class="tab-wrapper" v-for="(tab, index) in tabs2" :key="index" >
                    <div class="tab"
                        @click="selectTab2(index)"
                        :class="current2===index?'active':''">{{tab}}</div>
                </div>
            </div>
            <ve-line :data="chartData2" :settings="chartSetting2"></ve-line>
        </div>
        <!-- 新增客户数 -->
        <div class="section">
            <h2 class="title">新增客户数</h2>
            <div class="tabs">
                <div class="tab-wrapper" v-for="(tab, index) in tabs2" :key="index" >
                    <div class="tab"
                        @click="selectTab3(index)"
                        :class="current3===index?'active':''">{{tab}}</div>
                </div>
            </div>
            <ve-line :data="chartData3" :settings="chartSetting3"></ve-line>
        </div>
        <!-- 咨询客户数 -->
        <div class="section">
            <h2 class="title">咨询客户数</h2>
            <div class="tabs">
                <div class="tab-wrapper" v-for="(tab, index) in tabs2" :key="index" >
                    <div class="tab"
                         @click="selectTab4(index)"
                         :class="current4===index?'active':''">{{tab}}</div>
                </div>
            </div>
            <ve-line :data="chartData4" :settings="chartSetting4"></ve-line>
        </div>
        <!-- 近15日客户活跃趋势 -->
        <div class="section">
            <h2 class="title">近15日客户活跃趋势</h2>
            <ve-line :data="chartData5" :settings="chartSetting5"></ve-line>
        </div>
    </div>
</template>

<script>
import { getBossList } from "../../server/boss.js";
export default {
    data() {
        // 客户感兴趣的产品排行
        this.chartSetting1 = {
            radius: 60
        };
        // 商城订单量&交易金额设置
        this.chartSetting2 = {
            axisSite: { left: ["商城订单量"], right: ["交易金额"] },
            yAxisType: ["KMB", "KMB"],
            // yAxisName: ["交易金额", "商城订单量"]
        };
        // 新增客户量
        this.chartSetting3 = {
            yAxisType: ["KMB"]
        };
        //咨询客户数
        this.chartSetting4 = {
            axisSite: { left: ["电话咨询数"], right: ["微信咨询数"] },
            yAxisType: ["normal", "normal"]
        };
        // 近15日客户活跃趋势
        this.chartSetting5 = {};
        return {
            current1: 0,
            current2: 0,
            current3: 0,
            current4: 0,
            tabs: ["汇总", "昨日", "近七天", "近30天"],
            tabs2: ["近七天", "近15天", "近30天"],
            flag: 0,
            bossData: null,
            // 客户感兴趣的产品排行数据
            chartData1: null,
            //商城订单量&交易金额数据
            chartData2: null,
            //新增客户数
            chartData3: null,
            // 咨询客户数
            chartData4: null,
            //近15日客户活跃趋势
            chartData5: null
        };
    },
    created() {
        this._getBossList1();
        this._getBossList2();
        this._getBossList3();
        this._getBossList4();
        this._getBossList5();
        this._getBossList6();
    },
    methods: {

        //切换顶部7个数据
        selectTab1(index) {
            this.current1 = index;
            this._getBossList1();
        },
        // 切换商城订单量&交易金额的切换
        selectTab2(index) {
            this.current2 = index;
            this._getBossList3();
        },
        //切换新增客户数
        selectTab3(index) {
            this.current3 = index;
            this._getBossList4();
        },
        //切换咨询客户数
        selectTab4(index) {
            this.current4 = index;
            this._getBossList5();
        },

        // 获取顶部7个数据
        async _getBossList1() {
            let res = await getBossList({ type: "base", flag: this.current1 });
            if (res.data.status === 1) {
                this.bossData = res.data.data;
            }
        },
        // 客户感兴趣的产品排行数据
        async _getBossList2() {
            let res = await getBossList({ type: "likeproducts" });
            if (res.data.status === 1) {
                this.chartData1 = res.data.data;
            }
        },
        // 商城订单量&交易金额数据
        async _getBossList3() {
            let res = await getBossList({
                type: "orderlist",
                flag: this.current2
            });
            if (res.data.status === 1) {
                let data = {
                    columns: ["date", "商城订单量", "交易金额"],
                    rows: []
                };
                res.data.data.map((item, index) => {
                    data.rows.push({
                        date: item.date,
                        ['商城订单量']: item.orderQuantity,
                        ["交易金额"]: item.num
                    })
                });
                this.chartData2 = data;
            }
        },
        //新增客户数
        async _getBossList4() {
            let res = await getBossList({
                type: "userlist",
                flag: this.current3
            });
            if (res.data.status === 1) {
                let data = {
                    columns: ["date", "新增客户数"],
                    rows: []
                };
                res.data.data.map((item, index) => {
                    data.rows.push({
                        date: item.date,
                        ['新增客户数']: item.num
                    })
                });
                this.chartData3 = data;
            }
        },
        //咨询客户数
        async _getBossList5() {
            let res = await getBossList({
                type: "consultlist",
                flag: this.current4
            });
            if (res.data.status === 1) {
                let data = {
                    columns: ["date", "电话咨询数", "微信咨询数"],
                    rows: []
                };
                res.data.data.map((item, index) => {
                    data.rows.push({
                        date: item.date,
                        ['电话咨询数']: item.tel,
                        ['微信咨询数']: item.wx
                    })
                });
                this.chartData4 = data;
            }
        },
        //近15日客户活跃趋势
        async _getBossList6() {
            let res = await getBossList({
                type: "uvlist"
            });
            if (res.data.status === 1) {
                let data = {
                    columns: ["date", "近15日客户活跃趋势"],
                    rows: []
                };
                res.data.data.map((item, index) => {
                    data.rows.push({
                        date: item.date,
                        ['近15日客户活跃趋势']: item.num
                    })
                });
                this.chartData5 = data;
            }
        }
    }
};
</script>

<style lang='less' scoped>
@import url(./index.less);
</style>



